.homeBase {
  display: flex;
  width: 100%;
  min-height: 100vh;
}
.sidebar {
  width: 300px;
  background: #f4f7f9;
  position: relative;
  border-right: 1px solid #e6ecf1;
}
.container {
  width: calc(100% - 300px);
}
.mobileOnly {
  display: none;
}
@media only screen and (max-width: 900px) {
  .mobileOnly {
    display: block;
  }
  .homeBase {
    flex-direction: column;
  }
  .sidebar {
    display: none;
  }
  .container {
    width: 100%;
  }
}
