.sidebarBase {
  position: fixed;
  height: 100vh;
  width: 300px;
}
.pushLogo {
  width: 70px;
  margin-right: 10px;
}
.logoWrapper {
  display: flex;
  align-items: center;
  margin-left: 10px;
  border-bottom: 1px solid #e6ecf1;
}
.linksWrapper {
  padding-left: 30px;
  margin-top: 40px;
}
.link {
  padding: 10px;
  text-align: left;
  font-size: 16px;
}
.active {
  background: #fff;
  border: 1px solid #e6ecf1;
  border-right: none;
}
.activeLink {
  color: rgb(38, 203, 124) !important;
  font-family: ProximaNova-Bold;
}
