.contentBase {
  text-align: left;
  padding: 40px 20px 40px 60px;
  max-width: 900px;
}
.divider {
  width: 100%;
  border-bottom: solid 1px #ececec;
}
.infoBase {
  border-radius: 3px;
  border-left: solid 4px rgb(56, 132, 255);
  padding: 20px;
  background: #f5f7f9;
  display: flex;
  align-items: center;
}
.infoIcon {
  border: solid 3px;
  padding: 5px;
  width: 10px;
  height: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  border-color: #3e87ff;
  font-size: 14px;
  color: #3983ff;
  font-weight: 800;
}
.infoDescription {
  font-size: 16px;
  margin-left: 20px;
  color: #3b454e;
}
.fullImage {
  width: 600px;
  padding: 20px;
  margin: 40px 20px;
  box-shadow: 0px 0px 10px #b1b1b1;
  border-radius: 10px;
}

@media only screen and (max-width: 900px) {
  .contentBase {
    padding: 20px;
  }
  .fullImage {
    width: 80%;
  }
}
